.ul{
    width: 100%;
    height: 10%;
    background:rgb(221, 227, 233); 
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0;
    padding: 0;
    
}
.li{
    font-size: 4vh;
    padding:1.5vh 3vh;
    cursor: pointer;
    background: linear-gradient(rgb(196, 25, 2),rgb(243, 67, 67));
    font-weight: 400;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(221, 227, 233);
    transition: all 1s  ease;

}
.li:hover{
    transform: translateY(1px);
    color: rgb(173, 189, 204);
}
.active{
    background: linear-gradient(rgb(11, 66, 19),rgb(21, 84, 126));
    font-size: 4.2vh;
    
    cursor: pointer;
    padding:1.8vh 3.3vh;
    font-weight: 500;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(221, 227, 233);
    transition: all 1s  ease;
}
@media(max-width:1000){
    .li{
        font-size: 3.4vh;
        padding: 1.3vh 2.8vh
    }
}