.input{
    max-width: 80vh;
    max-height: 9vh;
    min-width: 60vh;
    min-height: 6vh;
    background: none;
    border: none;
    border-bottom: 2px solid #111;
    font-size: 3vh;
    outline: none;
    border-radius: 5px;
    color: aliceblue;
    
}
.notvalid{
    border-bottom: 2px solid rgb(241, 13, 13);
}
.select_inp{
    width: 60vh;
    height: 6vh;
    background: none;
    border: none;
    border-bottom: 2px solid #111;
    font-size: 3vh;
    outline: none;
    border-radius: 5px;
    color: aliceblue;
}
.select_inp option{
    color: #111
}