.Button{
    font-size: 4vh;
    background: rgb(236, 171, 152);
    border: none;
    outline: none;
    overflow: hidden;
    padding: 1vh 2vh;
    color: black;
    transition: all .5s ease;
    border-radius: 5px;
}
.Button:hover{
    transform: translateY(1px);
    background: rgb(236, 130, 98);
}
.Button:disabled{
    cursor: not-allowed;
    background: gray;
    color: rgb(66, 66, 66);
}
.Button:disabled:no-button{
}
.Button:disabled:hover{
    transform: translateY(0px);
    background: gray;
}