.header{
    width: 100%;
    height: 23%;
    position: relative;
    background:linear-gradient(rgb(196, 25, 2),rgb(243, 67, 67));
    display: flex;
    align-items: center;
    justify-content: center;
}
.h1{
    font-size: 9vh;
    font-weight: 400;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(221, 227, 233);
    transition: all 1s  ease;
   
}
.h1:hover{
    color: rgb(245, 212, 28);
    letter-spacing: 2px;
}
.nav{
    width: 30vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 15%;
    right: -5%;
    transform: translate(-50%,-50%)
}
.link{
    text-decoration: none;
    color: rgb(221, 227, 233);
    border-bottom: 1px solid rgb(221, 227, 233);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 3vh;
    white-space: pre;
    transition: all 1s ease;
    padding: 1vh 0;
}
.link:hover{
    color: rgb(245, 212, 28);
    border-bottom: 1px solid rgb(245, 212, 28);
    transform: translateY(2px)
}
.link:active{
    color: rgb(240, 204, 4);
    border-bottom: 1px solid rgb(240, 204, 3);
    transform: translateY(1px)
}