.Cardfood{
    width: 27%;
    height: 55vh;
    box-shadow: 0 1vh 1.3vh rgb(82, 79, 79);
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    background-size: cover;
    z-index: 2;
    transition: all 1s ease;
    margin-top: 2vh;
    margin-bottom: 7vh;
}

.im{
    width: 100%;
    height: 26vh;
    overflow: hidden;
    
}
.food_img{
   width: 100%;
   
}
.h3{
   
    font-size: 3vh;
  
    color: rgb(14, 15, 15);
    
    
}
.pragraf{
    
    position: relative;
    font-size: 2.5vh;
    border-top: 1px solid rgb(75, 71, 71);
    width: 100%;
    display: flex;
    font-size: 2.4vh;
    font-family: Arial, Helvetica, sans-serif; 
    align-items: center;
    justify-content: space-around;
    border-radius: 4px
}
.card_select{
    width: 70%;
    height: 4vh;

}
.select{
    width: 100%;
    height: 100%;
    background: inherit
    
}
.select option{
    font-family: Arial, Helvetica, sans-serif; 
    font-size: 2.5
}
.inp{
   
    position: absolute;
    left: 20vh;
}
.inp1{
    position: absolute;
    right: 8vh;
}
.add{
    font-size: 3vh;
    padding: 1.5vh 3vh;
    background: #eb5454;
    outline: none;
    border:1px solid #eb5454;
    transition: all .5s ease;
    margin:0 1vh;
    position: relative;
    border-radius: 5px
}

.add:hover{
    background: #f53b3b;
    transform: translateY(1px)
}
.add:active{
    background: #e61616
}
.btn{
    display: flex;
    justify-items: center;
    align-items: center;
}
.some_food{
    position: absolute;
    top: -1.4vh;
    left: -1.4vh;
    font-size: 2.3vh;
     width: 3vh;
     height: 3vh;
    background: #6b6868;
    border-radius: 50%
}
.ratings{
    width: 100%;
    height: 4vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}