.modal{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 90;
    background: rgba(82, 73, 73, 0.37);
}
.modal_box{
    width: 80vh;
    min-height: 55vh;
    background: rgb(236, 191, 185);
    font-size: 3vh;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 300;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 100;
    border-radius: 5px;
    color: blue;
}
.modal_button{
    margin-bottom: 4vh;
    
    display: flex;
    justify-content: space-around;
    
}
.button1{
    background: none;
    outline: none;
    border: none;
    font-size: 4vh;
    color: rgb(238, 70, 20);
    padding: 0 4vh;
}
.button2{
    background: none;
    outline: none;
    border: none;
    font-size: 4vh;
    color: rgb(20, 204, 29);
    padding: 0 4vh;
}
