.orders{
    width: 95%;
    height: 95%;
    overflow-y: scroll;
    background: inherit;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
}
.order{
    width: 80%;
    min-height: 30%;
    background: ivory;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    margin: 7vh;
}
.order_box{
    width: 100%;
    min-height: 100%;
    
}
.part{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.h3{
    font-size: 3vh;
    color: rgb(58, 58, 56);
    letter-spacing: 2px;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0 8vh;
    word-break: keep-all;
}
.span{
    font-size: 2vh;
    color: rgb(95, 95, 92);
    letter-spacing: 2px;
    font-family: Arial, Helvetica, sans-serif;
    word-break: break-all;
    padding-right:4vh ;
}